@import "./src/sass/helpers/var";
@import "./src/sass/helpers/animations";

.contact-us {
    display: grid;
    grid-template-columns: 1fr 2fr;
    width: 100%;
    align-items: flex-end;
    justify-content: space-between;
    margin-top: 5%;

    &__wrapper {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center; }

    &__legend {
        background: url('../img/legend.png') center / cover no-repeat;
        padding: 7.5% 7.5% 7.5% 32.5%;
        display: flex;
        justify-content: center;
        text-align: right;
        align-items: center;
        font-size: 1.1vw;
        line-height: 1.8vw;
        color: $white;
        font-weight: 600;
        position: relative;
        height: 50%;
        min-height: 17.5vw;
        width: 100%; } }

.form-contact {
    width: 100%;
    display: flex;
    padding: 0 5% 5%;
    justify-content: space-around;
    align-items: center;

    &--wrapper {
        display: flex;
        flex-direction: column;
        width: 100%;
        justify-content: space-between; }

    &__legend {
        font-size: 1vw;
        line-height: 1.5vw;
        padding: 1vw 0;
        color: $green;
        font-weight: 600;
        text-align: center; }

    &__title {
        padding-top: 1.5vw;
        font-size: 1.5vw;
        color: $black;
        display: flex;
        justify-content: center;
        font-weight: 600;
        text-align: center;
        justify-content: center;
        align-items: center; }

    &__input {
        margin: 2.5vw 0 0;
        padding: 1vw 1.5vw;
        font-size: 1vw;
        line-height: 1.5vw;
        width: 100%;
        border-radius: 10px;
        border: 1px solid $green;
        transition: $transition;
        &:hover {
            box-shadow: -10px 10px 15px 2.5px rgba($green-light, 0.15); } }

    &__btn {
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        width: 10vw;
        height: 10vw;
        border: none;

        font-size: 1.25vw;
        color: $white;
        background-color: $green;
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
        border-top-left-radius: 50%;
        border-bottom-left-radius: 50%;
        border-bottom-right-radius: 50%;

        margin: 0 auto;
        z-index: 5;
        padding: 7.5% 2.5%;
        white-space: inherit;
        text-transform: uppercase;
        transition: $transition;
        position: relative;

        box-shadow: -10px 10px 15px 2.5px rgba($black, 0.15);
        &:hover {
            cursor: pointer;
            box-shadow: -10px 10px 15px 2.5px rgba($green-light, 0.15); }

        &--wrapper {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            height: 100%;
            position: relative;
            margin: 2.5% auto;

            &::after {
                content: '';
                display: block;
                position: absolute;
                top: 0;
                left: 0;
                height: 100%;
                width: 100%;

                border-top-left-radius: 50%;
                border-bottom-left-radius: 50%;
                border-bottom-right-radius: 50%;
                background-image: url('../img/form_submit-wrapper.png');
                background-size: contain;
                background-repeat: no-repeat;
                background-position: center;
                animation: pulse 2s ease-in forwards infinite;
                transform: scale(1) translate(-50%, -50%); } } } }

.legend {
    &--wrapper {
        width: 37.5%; } }
.no-margin {
    margin: 0; }

@media screen and ( max-width: 1600px) {
    .contact-us {
        &__legend {
            font-size: 1vw; } } }

@media screen and ( max-width: 1366px) {
    .form-contact {
        &__input {
            margin-top: 1.5vw; } } }

@media screen and ( max-width: 1024px) {
    .form-contact {
        padding: 0 5% 5%; } }

@media screen and ( max-width: 768px) {
    .contact-us {
        position: relative;

        &__wrapper {
            display: none;
            position: absolute;
            width: 30%;
            bottom: 0;
            left: 0; }

        &__legend {
            font-size: 1.25vw;
            line-height: 2vw; } }

    .form-contact {
        grid-column: 1/3;
        grid-row: 1/2;
        flex-direction: column;
        padding: 0 5%;

        &--wrapper {
            width: 70%;
            margin: 0 auto; }

        &__legend {
            font-size: 2vw;
            line-height: 2.5vw; }
        &__title {
            padding: 1.5vw 0;
            font-size: 3.5vw; }

        &__input {
            padding: 2vw;
            margin: 1.5vw 0;
            font-size: 2vw; }
        &__btn {
            width: 15vw;
            height: 15vw;
            font-size: 2vw;
            margin: 0 auto;
            padding: 0;
            &--wrapper {
                margin: 5vw 0 5vw auto;
                &::after {
                    top: 0;
                    left: 0;
                    height: 100%;
                    transform: scale(0.65); } } } }

    .legend--wrapper {
        display: none; } }

@media screen and ( max-width: 480px) {

    .contact-us {
        &__wrapper {
            width: 75%; }

        &__legend {
            font-size: 3vw;
            line-height: 3.5vw;
            display: none; } }

    .form-contact {
        margin: 5vw 0;
        &--wrapper {
            width: 90%; }
        &__title {
            font-size: 5.25vw; }
        &__legend {
            line-height: 4.5vw;
            font-size: 3.5vw;
            padding: 5% 0; }
        &__input {
            padding: 5vw;
            font-size: 3.5vw; }
        &__btn {
            width: 25vw;
            height: 25vw;
            font-size: 3vw; } } }
