@media screen and ( max-width: 1600px) {
    .slider-list {
        &__item {
            & .more__element {
                & .content {
                    & .excerpt {
                        max-width: 30%;
                        font-size: 1.25vw; } } } } } }

@media screen and ( max-width: 1366px) {
    .slider-list {
        &__item {
            & .more__element {
                & .content {
                    max-width: 85%; } } } } }

@media screen and ( max-width: 1024px) {
    .slider-list {
        &__item {
            & .more__element {
                & .content {
                    & .excerpt {
                        max-width: 40%;
                        font-size: 1.9vw;
                        line-height: 2.5vw; }

                    & .link {
                        width: 15vw;
                        height: 15vw;

                        & a {
                            font-size: 1.5vw; } } } } } } }

@media screen and ( max-width: 768px) {
    .slider {
        height: 60vh;

        &__arrow_next,
        &__arrow_prev {
            width: 7.5vw;
            height: 7.5vw; } }
    .slider-list {
        &__item {
            & .more__element {
                & .content {
                    padding: 7.5% 0;

                    & .excerpt {
                        max-width: 40%;
                        font-size: 1.75vw;
                        line-height: 2.25vw; }
                    & .link {
                        width: 15vw;
                        height: 15vw;
                        & a {
                            font-size: 1.75vw; } } } } } } }

@media screen and ( max-width: 480px) {
    .slider {
        height: 80vh;
        &__nav-bar {
            min-height: 3.5vw; }

        &__arrow_prev,
        &__arrow_next {
            height: 12.5vw;
            width: 12.5vw; } }

    .slider-list {
        & .title__element {
            & .title {
                width: 80%;
                left: 50%;
                font-size: 7.5vw; } }
        &__item {
            & .more__element {
                & .content {
                    max-width: 100%;
                    padding: 7.5% 5%;
                    align-items: flex-end;
                    justify-content: space-between;

                    & .link {
                        width: 25vw;
                        height: 25vw;

                        a {
                            font-size: 3vw; } }

                    & .excerpt {
                        max-width: 70%;
                        font-size: 3.25vw;
                        line-height: 5vw;
                        text-align: center;
                        padding: 2.5%;
                        background-color: rgba($white, 0.4);
                        border-top-left-radius: 20px;
                        border-bottom-left-radius: 20px;
                        border-bottom-right-radius: 20px;
                        color: $green; } } }

            & .main__element,
            & .back__element,
            & .front__element {
                transform: scale(2) translate(5%, -25%);
                & img {
                    height: 50%;
                    transform: translate(-50%, 0); } } } } }
@media screen and (max-device-width: 768px) and (min-device-width: 320px) and (orientation: landscape) {
    .slider {
        height: 100vh; } }
