@import "./src/sass/helpers/var";
@import "./src/sass/helpers/animations";

.header {
  width: 100%;
  padding: 0 7.5%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin: 0 auto;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  transition: all 0.3s ease;
  overflow: hidden;
  animation: hideHeader 2s ease forwards;
  // clip-path: circle(8% at 12% 40%)
  background-color: $black;
  // background-color: transparent
  clip-path: url(#mask-circle);

  &--mask {
    position: fixed;
    // width: auto
    height: auto;

    & .circle--logo {
      display: inline-block;
      mix-blend-mode: multiply; } }

  img {
    &:nth-child(even) {
      position: absolute;
      bottom: 0;
      right: 0;
      width: 75%;
      height: 225%;
      transform: translateY(65%); } }

  &--position {
    animation: showHeader 1.5s ease forwards;

    & .navigation {
      align-items: center; }

    & .menu {
      span {
        background-color: $black;
        &::before,
        &::after {
          background-color: $black; } } }

    & .logo {
      border-radius: none;
      // transform: translateY(0)
      // background-color: transparent
      &__link {
        transition: $transition;
        transform: translateY(0);
        min-height: 5vw;
        background: center / contain no-repeat url('../img/logo_black.png'); }

      &__caption {
        color: $black;
        font-size: 1.5vw; } }

    & .chooseLanguage__item {
      color: $black;
      &:nth-child(2n) {
        &::before,
        &::after {
          background-color: $black; } } } }

  &--size {
    animation: showHeader 1.5s ease forwards;
    // align-items: baseline

    & .navigation {
      align-items: baseline; }

    & .menu {
      span {
        background-color: transparent;
        &::before,
        &::after {
          background-color: $black; } } }

    & .logo {
      border-radius: none;
      // background-color: transparent
      transform: translateY(0);
      &__link {
        transition: $transition;
        // transform: translateY(0)
        min-height: 5vw;
        background: center / contain no-repeat url('../img/logo_black.png'); }

      &__caption {
        color: $black;
        font-size: 1.5vw; } }

    & .chooseLanguage__item {
      color: $black;
      &:nth-child(2n) {
        &::before,
        &::after {
          background-color: $black; } } } } }

@media screen and (max-width: 1366px) {
  .header {
    padding: 0 7.5%; } }

@media screen and (max-width: 1224px) {
  .header {
    &--size {

      & .logo {
        &__caption {
          font-size: 2vw; } } }

    &--position {
      & .logo {
        &__link {
          padding: 2.5vw 0; }
        &__icon {
          transform: scale(1.35); }
        &__caption {
          font-size: 2vw;
          padding: 0.5vw 0; } } } } }

@media screen and (max-width: 1024px) {
  .header {
    &--size {
      & .logo {
        &__icon {
          transform: scale(1.5); }
        &__caption {
          font-size: 2vw; } } }

    &--position {
      & .logo {
        &__link {
          padding: 1.5vw 0; }
        &__icon {
          transform: scale(1.5); }
        &__caption {
          font-size: 2vw;
          padding: 0.25vw 0; } } } } }

@media screen and (max-width: 768px) {
  .header {
    &--size {
      min-height: 100vh;
      align-items: center;
      justify-content: space-around;
      flex-direction: column;

      & .navigation {
        justify-content: space-evenly;
        align-items: center; }

      & .logo {
        width: 15vw;
        &__link {
          min-height: 15vw; }
        &__caption {
          font-size: 3vw; } }

      & .navigation {
        min-height: 100%;
        flex-direction: column;
        flex-grow: 2;
        &--align {
          align-items: center;
          justify-content: space-around; }
        &--wrapper {
          flex-direction: column;
          align-items: center;
          padding: 0; }
        &--show {
          margin: 0; }
        &__item {
          text-align: center;
          align-items: center; }
        &__link {
          text-align: center;
          justify-content: center;
          font-size: 3.5vw;
          padding: 5vw 2.5vw; }

        & .chooseLanguage {
          display: none; } }

      & .submenu {
        display: none;
        width: 100%;
        align-self: center;
        &__link {
          text-align: center;
          justify-content: center;
          font-size: 2.5vw;
          padding: 2.5vw; } } }

    &--position {
      & .logo {
        &__link {
          min-height: 15vw; }
        &__caption {
          padding: 1vw 0;
          font-size: 3vw; } } } } }

@media screen and (max-width: 480px) {
  .header {
    padding: 5%;

    &--size {
      padding: 20% 0;
      & .logo {
        width: 30vw;
        &__caption {
          font-size: 5vw; } }

      & .navigation {
        &__link {
          font-size: 5vw;
          padding: 5vw 3.5vw; }

        & .chooseLanguage {
          display: none; } }

      & .submenu {
        &__link {
          font-size: 5vw;
          padding: 3.5vw; } } }

    &--position {
      & .logo {
        &__caption {
          font-size: 5vw;
          padding: 2vw 0; } } } } }
@media screen and (max-device-width: 768px) and (min-device-width: 320px) and (orientation: landscape) {
  .header {

    &--size {
      & .logo {
        width: 20vw;
        &__link {
          min-height: 10vw; } }

      & .navigation {
        &__link {
          font-size: 3vw;
          padding: 2vw 1.5vw; } }

      & .submenu {
        display: none;
        &__link {
          font-size: 5vw;
          padding: 1.5vw; } } }

    &--position {
      & .logo {
        &__caption {
          font-size: 5vw;
          padding: 2vw 0; } } } } }
