@keyframes down {
  0% {
    box-shadow: -10px 10px 15px 2.5px rgba($black, 0.15); }
  50% {
    transform: rotateZ(-15deg); }
  80% {
    transform: rotateZ(5deg); }
  100% {
    box-shadow: none; } }

@keyframes up {
  0% {
    box-shadow: none; }
  50% {
    transform: rotateZ(5deg); }
  80% {
    transform: rotateZ(-15deg); }
  100% {
    box-shadow: -10px 10px 15px 2.5px rgba($black, 0.15); } }

@keyframes show {
  0% {
    opacity: 0;
    visibility: hidden; }
  100% {
    opacity: 1;
    visibility: visible; } }

@keyframes hide {
  0% {
    opacity: 1;
    visibility: visible; }
  100% {
    opacity: 0;
    visibility: hidden; } }

@keyframes showModal {
  0% {
    opacity: 0;
    visibility: hidden;
    transform: translateX(100%); }
  100% {
    opacity: 1;
    visibility: visible;
    transform: translateX(0); } }

@keyframes hideModal {
  0% {
    opacity: 1;
    visibility: visible;
    transform: translateX(0); }
  20% {
    transform: translateX(5%); }
  100% {
    opacity: 0;
    visibility: hidden;
    transform: translateX(-100%); } }

@keyframes scaleItem {
  0% {
    transform: scale(1); }
  100% {
    transform: scale(1.025); } }

@keyframes unScaleItem {
  0% {
    transform: scale(1.025); }
  100% {
    transform: scale(1); } }

@keyframes fade {
  from {
    opacity: 0.1; }
  to {
    opacity: 1; } }

@keyframes pulse {
  0% {
    transform: scale(1); }
  50% {
    transform: scale(1.25); }
  100% {
    transform: scale(1); } }

